import { useState } from "react";
import axios from "axios";
import { useRouter } from "next/router";

const MAX_FILE_SIZE_MB = 5;
const ALLOWED_TYPES = ["pdf", "docx", "xlsx", "jpg", "jpeg", "png"];

export default function UploadPage() {
  const [file, setFile] = useState<File | null>(null);
  const [error, setError] = useState("");
  const [success, setSuccess] = useState("");
  const [loading, setLoading] = useState(false);

  const router = useRouter();
  const token = typeof window !== "undefined" ? localStorage.getItem("token") : null;

  const validateFile = (file: File): string | null => {
    const ext = file.name.split(".").pop()?.toLowerCase();
    const sizeMB = file.size / (1024 * 1024);

    if (!ext || !ALLOWED_TYPES.includes(ext)) {
      return "Tipo de archivo no permitido.";
    }
    if (sizeMB > MAX_FILE_SIZE_MB) {
      return "El archivo excede el tama√±o m√°ximo de 5 MB.";
    }
    return null;
  };

  const handleUpload = async () => {
    if (!file || !token) return;

    const validationError = validateFile(file);
    if (validationError) {
      setError(validationError);
      setSuccess("");
      return;
    }

    const formData = new FormData();
    formData.append("file", file);

    setLoading(true);
    try {
      const url = `${process.env.NEXT_PUBLIC_API_URL}/documents/upload`;
      console.log("üì§ Enviando archivo a:", url);

      const response = await axios.post(url, formData, {
        headers: {
          Authorization: `Bearer ${token}`,
          "Content-Type": "multipart/form-data"
        }
      });

      console.log("‚úÖ Respuesta del backend:", response.data);
      setSuccess("Archivo subido exitosamente.");
      setError("");
      setFile(null);
    } catch (err: any) {
      console.error("‚ùå Error al subir archivo:", err.response?.status, err.response?.data || err.message);
      setError("Error al subir el archivo.");
      setSuccess("");
    } finally {
      setLoading(false);
    }
  };

  return (
    <div style={{ maxWidth: 600, margin: "auto", paddingTop: "2rem" }}>
      <h2>Subir Documento</h2>

      <input
        type="file"
        onChange={e => setFile(e.target.files?.[0] || null)}
        style={{ marginBottom: "1rem" }}
      />

      <button onClick={handleUpload} disabled={!file || loading}>
        {loading ? "Subiendo..." : "Subir"}
      </button>

      {error && <p style={{ color: "red", marginTop: "1rem" }}>{error}</p>}
      {success && <p style={{ color: "green", marginTop: "1rem" }}>{success}</p>}
    </div>
  );
}
